import React from 'react';
import { CurrentUserContext} from '../contexts/CurrentUserContext';
import PopupWithForm from './PopupWithForm';

function EditProfilePopup  ({isOpen, onClose, onUpdateUser}) {

  const currentUser = React.useContext(CurrentUserContext);

  const [name, setName] = React.useState([]);
  const [description, setDescription] = React.useState([]);

  React.useEffect(() => {
    setName(currentUser.name);
    setDescription(currentUser.about);
  }, [currentUser]);

  function handleChangeName (evt) {
    setName(evt.target.value)
  }

  function handleChangeDescription (evt) {
    setDescription(evt.target.value)
  }

  function handleSubmit(e) {
    // –ó–∞–ø—Ä–µ—â–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –ø–µ—Ä–µ—Ö–æ–¥–∏—Ç—å –ø–æ –∞–¥—Ä–µ—Å—É —Ñ–æ—Ä–º—ã
    e.preventDefault();

    // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
    onUpdateUser({
      name: name,
      about: description,
    });
  }

  return(
    <PopupWithForm
    name="profile"
    title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
    submited = "–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
    isOpen={isOpen}
    onClose = {onClose}
    onSubmit = {handleSubmit}
  >
    <input type="text" id="popup_name" name="name" minLength="2" maxLength="40" pattern="[–∞-—è—ë–ê-–Ø–ÅA-Za-z \-]*" required placeholder="–ò–º—è, –§–∞–º–∏–ª–∏—è" className="popup__input" value={name|| ''} onChange={handleChangeName}/>
    <span className='popup__input-error popup__input-error_hidden' id='popup_name-input-error'></span>
    <input type="text" id="popup_lifestyle" name="about" minLength="2" maxLength="200" required value = {description || ''} onChange={handleChangeDescription}placeholder="–†–æ–¥ –∑–∞–Ω—è—Ç–∏–π/—Ö–æ–±–±–∏" className="popup__input"/>
    <span className='popup__input-error popup__input-error_hidden' id='popup_lifestyle-input-error'></span>
  </PopupWithForm>
  )
}

export default EditProfilePopup;
