import React from 'react';
import Header from './Header';
import Main from './Main';
import EditProfilePopup from './EditProfilePopup';
import EditAvatarPopup from './EditAvatarPopup';
import AddPlacePopup from './AddPlacePopup';
import PopupWithForm from './PopupWithForm';
import ImagePopup from './ImagePopup';
import Footer from './Footer';
import api from '../utils/Api';
import {CurrentUserContext} from '../contexts/CurrentUserContext';

function App() {

  const [cards, setCards] = React.useState([]);

  React.useEffect(()=>{
    api.getInitialCards()
    .then((Cards)=>{
      const cards = Cards.map(item =>{
        return{
          cardId:item._id,
          userId:item.owner._id,
          src:item.link,
          title:item.name,
          likes:item.likes,
          alt: item.name}
      })
      setCards(cards);

    })
    .catch(err => console.error(err));
  }, [])

  function handleCardLike(card) {
    // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
    const isLiked = card.likes.some(i => i._id === currentUser._id);
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    api.changeLikeCardStatus(card.cardId, !isLiked)
    .then((item) => {
      const newCard = {
        cardId:item._id,
        userId:item.owner._id,
        src:item.link,
        title:item.name,
        likes:item.likes,
        alt: item.name
      }
        // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
        const newCards = cards.map((c) => c.cardId === card.cardId ? newCard : c);
      // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
      setCards(newCards)
    })
    .catch(err => console.error(err));//–≤—ã–≤–µ–¥–µ–º –æ—à–∏–±–∫—É;
  }


  function handleCardDelete(card){
  api.deleteCard(card.cardId)
  .then(()=>{
    // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, —É–¥–æ–ª—è—è –∫–∞—Ä—Ç–æ—á–∫—É
    const newCards = cards.filter(—Å=>{return —Å.cardId!==card.cardId});
    // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
    setCards(newCards)
  })
  .catch(err => console.error(err));//–≤—ã–≤–µ–¥–µ–º –æ—à–∏–±–∫—É
  }

  const [currentUser, setCurrentUser] = React.useState({});

  const [EditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
  const  [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
  const  [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);

  React.useEffect(()=>{
    api.getUserData()
    .then((userData)=>{
      setCurrentUser(userData);
    })
    .catch(err => console.error(err));//–≤—ã–≤–µ–¥–µ–º –æ—à–∏–±–∫—É
  }, [])

  function closeAllPopups () {
    setIsAddPlacePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setIsEditProfilePopupOpen(false);
    setSelectedCard({});
  }

   function handleEditPlaceClick () {
    setIsAddPlacePopupOpen(true);
  }

  function handleEditAvatarClick () {
    setIsEditAvatarPopupOpen(true);
   }

   function handleEditProfileClick () {
     setIsEditProfilePopupOpen(true);
  }

  const [selectedCard, setSelectedCard] = React.useState({});
  function handleCardClick (card) {
    setSelectedCard(card);
  }


  function handleUpdateUser (currentUser) {
    api.patchUserInfo(currentUser)
    .then((updateUserInfo)=>{
      setCurrentUser(updateUserInfo);
      closeAllPopups();
    })
    .catch(err => console.error(err));//–≤—ã–≤–µ–¥–µ–º –æ—à–∏–±–∫—É

  }


  function handleUpdateAvatar (avatarUrl) {
    api.patchUserAvatar(avatarUrl)
    .then((userData)=>{
      setCurrentUser(userData)
      closeAllPopups ();
    })
    .catch(err => console.error(err));//–≤—ã–≤–µ–¥–µ–º –æ—à–∏–±–∫—É
  }


  function handleAddPlaceSubmit (popupData) {
    api.postNewCadr(popupData)
    .then((item)=>{
      const newCard = {
        cardId:item._id,
        userId:item.owner._id,
        src:item.link,
        title:item.name,
        likes:item.likes,
        alt: item.name
      }
      setCards([newCard, ...cards]);
      closeAllPopups ();
    })
    .catch(err => console.error(err));//–≤—ã–≤–µ–¥–µ–º –æ—à–∏–±–∫—É

  }

  return (
    <CurrentUserContext.Provider value = {currentUser}>
      <div className="page">
        <Header/>
        <Main
          cards = {cards}
          onCardLike = {handleCardLike}
          onCardDelete = {handleCardDelete}
          onEditProfile = {handleEditProfileClick}
          onAddPlace = {handleEditPlaceClick}
          onEditAvatar = {handleEditAvatarClick}
          setSelectedCard = {handleCardClick}
        />

        <EditProfilePopup
          isOpen={EditProfilePopupOpen}
          onClose={closeAllPopups}
          onUpdateUser={handleUpdateUser}
        />

        <EditAvatarPopup
          isOpen={isEditAvatarPopupOpen}
          onClose={closeAllPopups}
          onUpdateAvatar={handleUpdateAvatar}
        />

        <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose = {closeAllPopups}
          onAddPlace = {handleAddPlaceSubmit}
        />

        <PopupWithForm
          name = "confirm"
          title="–í—ã —É–≤–µ—Ä–µ–Ω—ã?"
          submited = "–î–∞"
          isOpen={false}
          onClose = {closeAllPopups}
        >
        </PopupWithForm>

        <ImagePopup setSelectedCard={handleCardClick} card = {selectedCard || ''} onClose = {closeAllPopups}/> */

        <Footer/>
    </div>
  </CurrentUserContext.Provider>


  );
}


export default App;
