
//–∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä API
class Api {
  constructor ({baseUrl, headers}) {
    this.baseUrl = baseUrl;
    this.headers = headers;
  }

  //–ø—Ä–∏–≤–∞—Ç–Ω—ã–π –º–µ—Ç–æ–¥ –ø—Ä–æ–≤–µ—Ä–∫–∏ –æ—Ç–≤–µ—Ç–∞ —Å–µ—Ä–≤–µ—Ä–∞ –∏ –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –∏–∑ json
  _transformResJson (res) {
      if (res.ok) {
        return res.json();
      }
      // –µ—Å–ª–∏ –æ—à–∏–±–∫–∞, –æ—Ç–∫–ª–æ–Ω—è–µ–º –ø—Ä–æ–º–∏—Å
      return Promise.reject(`–û—à–∏–±–∫–∞: ${res.status}`);
  }

  // –ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ –∑–∞–≥—Ä—É–∑–∫–∏ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
  getUserData () {
    return fetch(this.baseUrl + '/users/me', {
      headers: {
        authorization: this.headers
      }
    })

    .then(this._transformResJson)
  }

  //–ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ –∑–∞–≥—Ä—É–∑–∫–∏ –∫–∞—Ä—Ç–æ—á–µ–∫
  getInitialCards() {
    return fetch(this.baseUrl + '/cards', {
      headers: {
        authorization:  this.headers
      }
    })
    .then(this._transformResJson)
  }

  //–ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –æ—Ç—Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ—Ñ–∏–ª—è
  patchUserInfo(popupData) {
    return fetch(this.baseUrl + '/users/me', {
      method: 'PATCH',
      headers: {
        authorization:  this.headers,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
      name: popupData.name,
      about: popupData.about
     })
    })
    .then(this._transformResJson)
  }

  //–ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  postNewCadr (popupData) {
   return fetch (this.baseUrl + '/cards', {
    method: 'POST',
    headers: {
      authorization: this.headers,
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      name: popupData.place,
      link:popupData.place_url
      })
    })
    .then(this._transformResJson)
  }

  //–ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ —É–¥–æ–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
  deleteCard (cardId) {
    return fetch (this.baseUrl + '/cards/' + cardId, {
      method: 'DELETE',
      headers: {
        authorization: this.headers,
      }
    })
    .then(this._transformResJson)
  }

  //–ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ –ø–æ—Å—Ç–∞–Ω–æ–≤–∫–∏ –∏ —É–¥–æ–ª–µ–Ω–∏—è –ª–∞–π–∫–∞
  changeLikeCardStatus (cardId, isLiked) {
     if (isLiked) {
      return fetch (this.baseUrl + '/cards/likes/' + cardId, {
        method: 'PUT',
        headers: {
          authorization: this.headers,
        }
      })
      .then(this._transformResJson)
      } else {
        return fetch (this.baseUrl + '/cards/likes/' + cardId, {
          method: 'DELETE',
          headers: {
            authorization: this.headers,
          }
        })
        .then(this._transformResJson)
      }
  }

  //–ø—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  patchUserAvatar (userAvatarUrl) {
    return fetch (this.baseUrl + '/users/me/avatar/', {
      method: 'PATCH',
      headers: {
      'Content-Type': 'application/json',
        authorization: this.headers,
      },
      body: JSON.stringify({
        avatar: userAvatarUrl,
      })
    })
    .then(this._transformResJson)
  }
}

const api = new Api  ({
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-14',
  headers: 'cf883479-ba69-4170-b793-165731887c32',
})

export default api;
